import "core-js/modules/es.array.push.js";
import "core-js/modules/es.set.difference.v2.js";
import "core-js/modules/es.set.intersection.v2.js";
import "core-js/modules/es.set.is-disjoint-from.v2.js";
import "core-js/modules/es.set.is-subset-of.v2.js";
import "core-js/modules/es.set.is-superset-of.v2.js";
import "core-js/modules/es.set.symmetric-difference.v2.js";
import "core-js/modules/es.set.union.v2.js";
import { mapGetters } from 'vuex';
import DashboardTabs from '@/components/DashboardTabs.vue';
export default {
  name: 'CustomerConfig',
  data() {
    return {
      tableData: [],
      dealedData: [],
      // 根据IP地址处理后的数据，格式为 {ip, count, [data]}
      blockipList: [] // 禁止上传的IP列表
    };
  },
  components: {
    DashboardTabs
  },
  computed: {
    ...mapGetters(['credentials']),
    disableTooltip() {
      return window.innerWidth < 768;
    }
  },
  methods: {
    async fetchWithAuth(url, options = {}) {
      // 开发环境, url 前面加上 /api
      // url = `/api${url}`;
      if (this.credentials) {
        // 设置 Authorization 头
        options.headers = {
          ...options.headers,
          'Authorization': `Basic ${this.credentials}`
        };
        // 确保包含凭据，如 cookies
        options.credentials = 'include';
      }
      const response = await fetch(url, options);
      if (response.status === 401) {
        // Redirect to the login page if a 401 Unauthorized is returned
        this.$message.error('认证状态错误，请重新登录');
        this.$router.push('/adminLogin');
        throw new Error('Unauthorized');
      }
      return response;
    },
    dealByIP(data) {
      let dealedData = [];
      let ipSet = new Set();
      data.forEach(item => {
        if (item.metadata?.UploadIP) {
          ipSet.add(item.metadata.UploadIP);
        }
      });
      ipSet.forEach(ip => {
        let ipData = data.filter(item => item.metadata?.UploadIP === ip);
        let count = ipData.length;
        let enable = !this.blockipList.includes(ip);
        dealedData.push({
          ip,
          count,
          data: ipData,
          enable
        });
      });
      return dealedData;
    },
    handleLogout() {
      this.$store.commit('setCredentials', null);
      this.$router.push('/adminLogin');
    },
    formatTimeStamp(timeStamp) {
      return new Date(timeStamp).toLocaleString();
    },
    sortByTimestamp(a, b) {
      return new Date(a.metadata.TimeStamp) - new Date(b.metadata.TimeStamp);
    },
    async handleSwitchEnable(row) {
      const ip = row.ip;
      const enable = row.enable;
      if (enable) {
        // 从 blockipList 中移除
        this.blockipList = this.blockipList.filter(item => item !== ip);
        // 更新 blockipList
        await this.fetchWithAuth("/api/manage/cusConfig/whiteip", {
          method: 'POST',
          body: ip
        });
      } else {
        // 添加到 blockipList 中
        this.blockipList.push(ip);
        // 更新 blockipList
        await this.fetchWithAuth("/api/manage/cusConfig/blockip", {
          method: 'POST',
          body: ip
        });
      }
    }
  },
  mounted() {
    this.fetchWithAuth("/api/manage/check", {
      method: 'GET'
    }).then(response => response.text()).then(result => {
      if (result == "true") {
        this.showLogoutButton = true;
        // 在 check 成功后再执行 list 的 fetch 请求
        return this.fetchWithAuth("/api/manage/list", {
          method: 'GET'
        });
      } else if (result == "Not using basic auth.") {
        return this.fetchWithAuth("/api/manage/list", {
          method: 'GET'
        });
      } else {
        throw new Error('Unauthorized');
      }
    }).then(response => response.json()).then(async result => {
      // 读取blockipList, 接口返回格式为 'ip1,ip2,ip3'，需要转换为数组
      const blockipList = await this.fetchWithAuth("/api/manage/cusConfig/blockipList", {
        method: 'GET'
      });
      this.blockipList = (await blockipList.text()).split(',');
      this.tableData = result;
      this.dealedData = this.dealByIP(result); // 根据IP地址处理数据
    }).catch(err => {
      if (err.message !== 'Unauthorized') {
        this.$message.error('同步数据时出错，请检查网络连接');
      }
    });
  }
};